@import url(https://fonts.googleapis.com/css2?family=Dela+Gothic+One&display=swap);
:root {
  --bgtime: 3s;

  /* brand hues */
  --brand-h: 341.6;
  --indigo-h: 222.2;
  --navy-h: 210.9;
  --teal-h: 184.9;
  --brown-h: 356.3;
  --salmon-h: 7.8;
  --dark-h: 210;
  --base1-h: 165;
  --base2-h: 195;

  /* compiled defaults */
  --bg: hsl(0, 0%, 100%);
  --fg: hsl(0, 0%, 0%);
  --brand: hsl(var(--brand-h), 100%, 44.7%);
  --indigo: hsl(var(--indigo-h), 37.4%, 19.4%);
  --navy: hsl(var(--navy-h), 49.7%, 39%);
  --teal: hsl(var(--teal-h), 19.4%, 62.5%);
  --brown: hsl(var(--brown-h), 13.1%, 23.9%);
  --salmon: hsl(var(--salmon-h), 100%, 72.7%);
  --dark: hsl(var(--dark-h), 33.3%, 9.4%);
  --base1: hsl(var(--base1-h), 6.6%, 76.1%);
  --base2: hsl(var(--base2-h), 4.4%, 82.4%);
}

body {
  overflow: hidden;
}
body {
  background-color: #205efe;

  height: 100vh;
}
.make-logo {
  position: absolute;
  z-index: 99999;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
}

#bgart {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  margin: 0;
}

#bgart circle {
  fill: transparent;
  stroke: url(#bggrad);
  stroke-linecap: butt;
  -webkit-animation: bgmove 3s linear infinite;
          animation: bgmove 3s linear infinite;
  -webkit-animation: bgmove var(--bgtime) linear infinite;
          animation: bgmove var(--bgtime) linear infinite;
}

#bggrad [offset="0%"] {
  stop-color: hsl(341.6, 100%, 44.7%);
  stop-color: var(--brand);
}

#bggrad [offset="100%"] {
  stop-color: hsl(7.8, 100%, 72.7%);
  stop-color: var(--salmon);
}

@-webkit-keyframes bgmove {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes bgmove {
  0% {
    stroke-dashoffset: 1000;
  }

  100% {
    stroke-dashoffset: 0;
  }
}
.make-logo {
  color: hsl(0, 0%, 100%);
  color: var(--bg);
  mix-blend-mode: exclusion;
  pointer-events: none;
  opacity: 0;
  -webkit-animation: showlogo 1s 4s cubic-bezier(0.42, 0, 0, 1.04) forwards;
          animation: showlogo 1s 4s cubic-bezier(0.42, 0, 0, 1.04) forwards;
}

h1 {
  font-family: 'Dela Gothic One', cursive;
  color: white;
  text-align: left;
  position: absolute;
  top: -1rem;
  left: -1rem;
  z-index: 100;
  line-height: 0.9;
  font-size: 10rem;
  text-transform: uppercase;
  -webkit-animation: showHeadline 2s 3s cubic-bezier(0.42, 0, 0, 1.04) forwards;
          animation: showHeadline 2s 3s cubic-bezier(0.42, 0, 0, 1.04) forwards;
}

@-webkit-keyframes showlogo {
  0% {
    -webkit-filter: blur(1000px);
            filter: blur(1000px);
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }

  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes showlogo {
  0% {
    -webkit-filter: blur(1000px);
            filter: blur(1000px);
    opacity: 0;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }

  100% {
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes showHeadline {
  0% {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }

  100% {
    -webkit-transform: translateY(-100rem);
            transform: translateY(-100rem);
  }
}

@keyframes showHeadline {
  0% {
    -webkit-transform: translateY(0rem);
            transform: translateY(0rem);
  }

  100% {
    -webkit-transform: translateY(-100rem);
            transform: translateY(-100rem);
  }
}

p {
  pointer-events: none;
  text-align: Center;
  color: white;
  font-family: sans-serif;
  position: relative;
  z-index: 999;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

